export default {
	data() {
		return {
			email:'',
			password: '',
			disable:true,
			loading:false
		}
	},
	watch:{//监听属性
		password(val){
			console.log(val);
			this.wa();
		},
		email(val){
			this.wa();
		}	
	},
	methods:{
		wa(){//监听input框内容是否为空
			if(this.password==''||this.email==""){
				this.disable=true;
				return false;
			}
			// 验证邮箱正则
			let em=/^([A-Za-z0-9_\+\.])+\@([A-Za-z0-9_\+\.])+\.([A-Za-z]{2,4})$/;
				if(!em.test(this.email)){//如果不为true
					uni.showToast({
						title: '请输入正确的邮箱格式',
						icon:'none'
					});
					this.disable=true;
					return false;
				}
			this.disable=false;
		},
		check(){//输入框判断
			//输入框都等于空
			if(this.email==''){
				uni.showToast({
					title: '请输入邮箱账号',
					icon:'none'
				});
				return false;
			}
			// 验证邮箱正则
			// let em=/^([A-Za-z0-9_\+\.])+\@([A-Za-z0-9_\+\.])+\.([A-Za-z]{2,4})$/;
			// if(!em.test(this.email)){//如果不为true
			// 	uni.showToast({
			// 		title: '请输入正确的邮箱格式',
			// 		icon:'none'
			// 	});
			// 	return false;
			// }
			if(this.password==''){
				uni.showToast({
					title: '请输入密码',
					icon:'none'
				});
				return false;
			}
			
		},
		sub(){//提交
			this.loading=true;
			this.disable=true;
			// if(!this.check()){
			// 	this.loading=true;
			// 	this.disable=true;
			// 	return false
			// };
			setTimeout(()=>{
				uni.showToast({
					title: '提交成功',
					icon:'success'
				});
				this.loading=false;
				this.disable=false;
			},2000)
		}
	}
}
